import Vue from 'vue'
import Router from 'vue-router'
import OcLayout from '@/share/layout'
import OcHome from '@/views/home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: OcLayout,
      children: [{
        path: '/',
        name: 'OcHome',
        component: OcHome
      }]
    }
  ]
})



