<template>
  <div class="oc-card-swiper">
    <div class="cover">
      <swiper :options="coverOption" ref="coverSwiper">
        <swiper-slide v-for="(slide, index) in swiperList" :key="index">
          <div class="cover-item" :style="coverBackground(slide.cover)"></div>
        </swiper-slide>
      </swiper>
    </div>
    <div class="info">
      <swiper :options="infoOption" ref="infoSwiper" @slideChangeTransitionStart="sliderChange()">
        <swiper-slide v-for="(slide, index) in swiperList" :key="index">
          <dl class="info-item">
            <dt>{{slide.title}}</dt>
            <dd>{{slide.summary}}</dd>
          </dl>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-button-prev" slot="button-prev">
          <i class="fa fa-arrow-left"></i>
        </div>
        <div class="swiper-button-next" slot="button-next">
          <i class="fa fa-arrow-right"></i>
        </div>
      </swiper>
    </div>
  </div>
  <!-- swiper -->
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  name: 'CardSwiper',
  components: {
    swiper,
    swiperSlide
  },
  props: {
    swiperList: Array
  },
  data () {
    return {
      infoOption: {
        loop: true,
        autoplay: {
          delay: 5000,
          disableOnInteraction: false
        },
        spaceBetween: 20,
        pagination: {
          el: '.info .swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.info .swiper-button-next',
          prevEl: '.info .swiper-button-prev'
        }
      },
      coverOption: {
        effect: 'fade',
        simulateTouch: false
      }
    }
  },
  computed: {
    infoSwiper () {
      return this.$refs.infoSwiper.swiper
    },
    coverSwiper () {
      return this.$refs.coverSwiper.swiper
    }
  },
  methods: {
    sliderChange () {
      let swiperIndex = this.infoSwiper.activeIndex
      this.coverSwiper.slideTo(swiperIndex)
    },
    coverBackground (url) {
      let coverStyle = {backgroundImage: 'url(' + url + ')'}
      return coverStyle
    }
  }
}
</script>

<style lang="scss" scoped>
.oc-card-swiper {
  border-radius: 10px;
  padding: 20px;
  width: 100%;
  background: $--oc-color-white;
  box-shadow: $--oc-box-shadow-light;
  -webkit-box-shadow: $--oc-box-shadow-light;
  overflow: hidden;
  .cover {
    .cover-item {
      width: 100%;
      border-radius: 10px;
      padding-bottom: 80%;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
  .info {
    .swiper-container {
      padding-bottom: 50px;
      .swiper-pagination {
        left: 0;
        bottom: 10px;
        width: auto;
        overflow: hidden;
      }
      .swiper-button-prev {
        top: auto;
        left: auto;
        right: 60px;
        bottom: 0;
        background-image: none;
        width: 50px;
        height: 50px;
        text-align: center;
        line-height: 50px;
        font-size: 16px;
        color: $--oc-color-white;
        border-radius: 10px;
        background-color: $--oc-color-theme;
      }
      .swiper-button-next {
        top: auto;
        right: 0;
        bottom: 0;
        width: 50px;
        height: 50px;
        text-align: center;
        line-height: 50px;
        font-size: 16px;
        color: $--oc-color-white;
        border-radius: 10px;
        background-image: none;
        background-color: $--oc-color-theme;
      }
      .info-item {
        padding: 20px 0;
        dt {
          font-size: 24px;
          line-height: 32px;
          font-weight: bolder;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
        dd {
          margin-top: 20px;
          font-size: 14px;
          line-height: 22px;
          height: 110px;
          color: $--oc-color-text-regular;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 5;
          overflow: hidden;
        }
      }
    }
  }
}

</style>



