<template>
  <div class="oc-container">
    <div class="oc-card clear-both">
      <div class="oc-card-news col-box-4">
        <card-swiper :swiperList="swiperList()"></card-swiper>
        <!-- swiper -->
      </div>
      <!-- news -->
      <div class="oc-card-main col-box-8">
        <div class="oc-card-column col-box-6" v-for="(item,index) in cardList" :key="index">
          <div class="oc-card-item">
            <div class="oc-card-cover">
              <img :src="item.cover">
              <span>{{item.title}}</span>
            </div>
            <!-- prev -->
            <div class="oc-card-info">
              <dl>
                <dt>
                  <span :class="item.type">{{setType(item.type)}}</span>
                </dt>
                <dd>{{item.date}}</dd>
              </dl>
              <!-- dl -->
              <p>{{item.summary}}</p>
              <!-- p -->
            </div>
            <!-- info -->
          </div>
          <!-- item -->
        </div>
        <!-- column -->
      </div>
      <!-- main -->
    </div>
    <!-- card -->
  </div>
</template>

<script>
import CardSwiper from '@/components/cardSwiper'

export default {
  name: 'OcHome',
  components: {
    CardSwiper
  },
  data () {
    return {
      cardList: [
        {
          title: '基于 CPU 的 AI 深度优化，如何打造业内领先的 MLPerf 推理性能？',
          cover: '../../../static/images/cover-1.jpg',
          type: 'type-1',
          date: '2019/12/27 17:01',
          summary: '几天前，英特尔正式宣布以 20 亿美元收购成立仅三年的 AI 芯片公司 Habana Labs。这是继收购 Mobileye、Nervana 之后，英特尔在 AI 领域第三次收购 AI 初创公司。与此同时，腾讯也在不断加大 AI 布局。两强联手，看科技巨头如何引领未来变革？'
        },
        {
          title: 'XGBoost 在携程搜索排序中的应用',
          cover: '../../../static/images/cover-2.jpg',
          type: 'type-2',
          date: '2019/12/27 17:01',
          summary: '在互联网高速发展的今天，越来越复杂的特征被应用到搜索中，对于检索模型的排序，基本的业务规则排序或者人工调参的方式已经不能满足需求了，此时由于大数据的加持，机器学习、深度学习成为了一项可以选择的方式。'
        },
        {
          title: 'AI 芯片年终盘点：有些公司年产 N 片，有些 N 年产 1 片都难',
          cover: '../../../static/images/cover-3.jpg',
          type: 'type-3',
          date: '2019/12/27 17:01',
          summary: '过去几年，人工智能产业无论是算法实现、海量数据获取和存储还是计算能力的体现都离不开目前唯一的物理基础——芯片。可以说，“无芯片不 AI”，能否开发出具有超高运算能力、符合市场需求的芯片，已成为人工智能领域可持续发展的重要因素。 年终在即，AI 前线小组盘点了 2019 年国内外主流科技公司在 AI 芯片方面的进展，有些公司因为 2019 年没有发布新的 AI 芯片而未计入本次统计，统计维度分为主要特点、芯片参数和应用场景三部分，如有疏漏，欢迎各位留言。'
        },
        {
          title: '敏捷开发落地不实际？原因可能在于你的 IDE 工具',
          cover: '../../../static/images/cover-4.jpg',
          type: '',
          date: '2019/12/27 17:01',
          summary: '对于企业来说，效率就是一切。开发效率的工程化建设已经开始被各大企业提到技术管理日程中。而且现阶段，无论是框架也好、模板也好，目的都是在为提升代码开发效率而努力。随着云计算的深入，端 + 云的开发模式以及完全云端化的开发模式都先后上线，这些无疑都是在对传统 IDE 开发模式的挑战。云端 IDE，会是未来的趋势吗？'
        },
        {
          title: 'Facebook 首席 AI 科学家：机器学习的杀手级应用是 AR 眼镜',
          cover: '../../../static/images/cover-5.jpg',
          type: 'type-1',
          date: '2019/12/27 17:01',
          summary: '想要实现理想中的 AR 眼镜，还需要面临一系列的挑战，尤其是在能效比方面，越来越多的开发者开始去考虑低功耗的问题。近期，关于机器学习的应用场景，Facebook 首席 AI 科学家 Lann LeCun 就曾表示，AR 眼镜将是机器学习从业者的绝佳挑战领域，它将成为高效节能的机器学习杀手级应用。'
        }
      ]
    }
  },
  methods: {
    setType (type) {
      let infoType = type
      let infoContent = ''
      switch (infoType) {
        case 'type-1':
          infoContent = 'Skill'
          break
        case 'type-2':
          infoContent = 'ACG'
          break
        case 'type-3':
          infoContent = 'Pet'
          break
        default:
          infoContent = 'Others'
      }
      return infoContent
    },
    swiperList () {
      let newCardList = this.makeRandomArr(this.cardList, 4)
      return newCardList
    },
    makeRandomArr (arrList, num) {
      var tempArr = arrList.slice(0)
      var newArrList = []
      if (num > arrList.length) {
        newArrList = arrList
      } else {
        for (var i = 0; i < num; i++) {
          var random = Math.floor(Math.random() * (tempArr.length - 1))
          var arr = tempArr[random]
          tempArr.splice(random, 1)
          newArrList.push(arr)
        }
      }
      return newArrList
    }
  }
}
</script>

<style lang="scss" scoped>
.oc-container {
  .oc-card {
    padding: 20px;
    margin: 0 -20px;
    .oc-card-news {
      padding: 20px;
    }
    .oc-card-column {
      float: left;
      padding: 20px;
      .oc-card-item {
        .oc-card-cover {
          position: relative;
          border-radius: 10px;
          width: 100%;
          padding-bottom: 50%;
          background: $--oc-color-black;
          box-shadow: $--oc-box-shadow-light;
          -webkit-box-shadow: $--oc-box-shadow-light;
          overflow: hidden;
          img {
            position: absolute;
            top: 50%;
            left: 50%;
            min-width: 100%;
            min-height: 100%;
            opacity: .7;
            transform: translate3d(-50%, -50%, 0);
            -webkit-transform: translate3d(-50%, -50%, 0);
            z-index: 1;
          }
          span {
            position: absolute;
            left: 0;
            bottom: 0;
            display: block;
            padding: 20px;
            font-size: 16px;
            line-height: 24px;
            color: $--oc-color-white;
            width: 100%;
            z-index: 2;
          }
        }
        .oc-card-info {
          border-radius: 10px;
          padding: 20px;
          width: 100%;
          background: $--oc-color-white;
          box-shadow: $--oc-box-shadow-light;
          -webkit-box-shadow: $--oc-box-shadow-light;
          overflow: hidden;
          dl {
            margin-bottom: 10px;
            dt {
              margin-bottom: 5px;
              overflow: hidden;
              span {
                display: block;
                float: left;
                font-size: 12px;
                line-height: 20px;
                white-space: nowrap;
                padding: 0 15px;
                border-radius: 10px;
                color: $--oc-color-white;
                background: $--oc-color-text-placeholder;
                &.type-1 {
                  background: $--oc-color-primary;
                }
                &.type-2 {
                  background: $--oc-color-success;
                }
                &.type-3 {
                  background: $--oc-color-danger;
                }
              }
            }
            dd {
              font-size: 12px;
              line-height: 20px;
              color: $--oc-color-text-secondary;
            }
          }
          p {
            font-size: 14px;
            line-height: 22px;
            height: 66px;
            color: $--oc-color-text-regular;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            overflow: hidden;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1280px) {

}
@media screen and (max-width: 1000px) {

}
@media screen and (max-width: 768px) {

}
</style>



