<template>
  <div class="layout">
    <div class="oc-header">
      <oc-header></oc-header>
    </div>
    <!-- header -->
    <div class="oc-content">
      <router-view></router-view>
    </div>
    <!-- content -->
    <div class="oc-footer">
      <oc-footer></oc-footer>
    </div>
    <!-- footer -->
  </div>
</template>

<script>
import OcHeader from '@/share/header'
import OcFooter from '@/share/footer'

export default {
  name: 'OcLayout',
  components: {
    OcHeader,
    OcFooter
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="scss" scoped>
.layout {
  .oc-header {
    padding: 5px 0;
    background: $--oc-color-white;
    box-shadow: $--oc-box-shadow-light;
    -webkit-box-shadow: $--oc-box-shadow-light;
  }
}
</style>



