<template>
  <div class="oc-container clear-both">
    <div class="oc-logo">
      <img src="@/assets/images/logo.png">
    </div>
    <div class="oc-nav">
      <div class="oc-nav-content">
        <ul class="clear-both">
          <li
            v-for="(item,index) in navList"
            :key="index"
            :class="{'active':index===navActive}"
            @click="navClick(index)"
          >
            <a>{{item.title}}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'OcHeader',
  data () {
    return {
      navActive: 0,
      navList: [{
        title: 'home',
        type: ''
      }, {
        title: 'skill',
        type: 'type-1'
      }, {
        title: 'acg',
        type: 'type-2'
      }, {
        title: 'pet',
        type: 'type-3'
      }]
    }
  },
  methods: {
    navClick (index) {
      this.navActive = index
    }
  }
}
</script>

<style lang="scss" scoped>
@mixin n-items($n) {
  &:first-of-type:nth-last-of-type(#{$n}),
  &:first-of-type:nth-last-of-type(#{$n}) ~ &{
    width: calc(100% / #{$n});
  }
}
.oc-container {
  .oc-logo {
    float: left;
    width: 50px;
    height: 50px;
    background: $--oc-color-theme;
    border-radius: 100%;
    overflow: hidden;
    padding: 5px;
    cursor: pointer;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .oc-nav {
    float: right;
    li {
      position: relative;
      float: left;
      font-size: 16px;
      line-height: 30px;
      padding: 10px 20px;
      color: $--oc-color-text-regular;
      transition: $--oc-all-transition;
      -moz-transition: $--oc-all-transition;
      -o-transition: $--oc-all-transition;
      -webkit-transition: $--oc-all-transition;
      cursor: pointer;
      &::after {
        position: absolute;
        bottom: 0;
        left: 50%;
        right: 50%;
        display: block;
        content: "";
        height: 0;
        border-radius: 2px;
        opacity: 0;
        background: $--oc-color-theme;
        transition: $--oc-all-transition;
        -moz-transition: $--oc-all-transition;
        -o-transition: $--oc-all-transition;
        -webkit-transition: $--oc-all-transition;
      }
      &:hover {
        color: $--oc-color-theme;
      }
      &.active {
        color: $--oc-color-theme;
        &::after {
          left: 20px;
          right: 20px;
          height: 4px;
          opacity: 1;
        }
      }
    }
  }
}
@media screen and (max-width: 1280px) {

}
@media screen and (max-width: 1000px) {

}
@media screen and (max-width: 768px) {
  .oc-container {
    .oc-logo {
      float: none;
      margin: 0 auto;
    }
    .oc-nav {
      float: none;
      overflow: hidden;
      .oc-nav-content {
        margin-bottom: -30px;
        padding-bottom: 30px;
        overflow: auto;
        ul {
          display: table;
          min-width: 100%;
          li {
            float: none;
            display: table-cell;
            text-align: center;
            white-space: nowrap;
          }
        }
      }
    }
  }
}
</style>



